import React, {useEffect} from 'react';
import {
    Button,
    Table,
} from "antd";
import {
    SelectDataModal,
} from '../../components'
import {connect} from "react-redux";
import {
    useWudiReducer,
} from '../../utils/hook';
import {changLoadingState} from "../../actions/global";


const iniDataSource = [];
for (let i = 0; i < 46; i++) {
    iniDataSource.push({
        key: i,
        name: `Edward King ${i}`,
        age: 32,
        address: `London, Park Lane no. ${i}`,
    });
}
const mapState = state => ({
    loading: state.global.isLoading,
});
function Index(props) {
    const [dao, hit] = useWudiReducer({
        visible: false,
        selectedRowKeys: [],
        current: 1,
        pageSize: 10,
        dataSource: iniDataSource,
        selectDataSource: [],
    });

    useEffect(() => {
        props.changLoadingState();
        return () => props.changLoadingState();
    }, [])

    function onSelectChange(selectedRowKeys) {
        hit({ selectedRowKeys });
    };

    // 模态框内容
    const columns = [
        {
            title: 'Name',
            dataIndex: 'name',
        },
        {
            title: 'Age',
            dataIndex: 'age',
        },
        {
            title: 'Address',
            dataIndex: 'address',
        },
    ];
    const rowSelection = {
        selectedRowKeys: dao.selectedRowKeys,
        onChange: onSelectChange,
        getCheckboxProps: record => ({
            disabled: record.disabled,
        }),
    };
    const pagination = {
        total: dao.dataSource.length,
        current: dao.current,
        pageSize: dao.pageSize,
        showQuickJumper: true,
        showSizeChanger: true,
        onChange: current => {hit({current})},
        onShowSizeChange: (current, pageSize) => {
            hit({
                current,
                pageSize,
            })
        },
    };
    const modalContent = (
        <Table
            rowSelection={rowSelection}
            columns={columns}
            dataSource={dao.dataSource}
            pagination={pagination}
            onRow={record => {
                return {
                    style: { cursor: 'pointer',},
                    onClick: () => {
                        let keys = [];
                        if (dao.selectedRowKeys.includes(record.key)) {
                            keys = dao.selectedRowKeys.filter(i => i !== record.key);
                        } else {
                            keys = [...dao.selectedRowKeys].concat(record.key);
                        }
                        if (!record.disabled) {
                            hit({
                                selectedRowKeys: keys,
                            })
                        }
                    }
                }
            }}
        />
    );
    return (
        <div>
            <Button
                onClick={() => hit({visible: true})}
            >showSelectDataModal</Button>
            <SelectDataModal
                visible={dao.visible}
                title={'test'}
                onOk={(data) => {
                    const selectDataSource = dao.dataSource.filter(item => dao.selectedRowKeys.includes(item.key));
                    const newDataSource = dao.dataSource.map(item => {
                        dao.selectedRowKeys.includes(item.key)
                            ? item.disabled = true : ''
                        return item;
                    });
                    hit({
                        visible: false,
                        dataSource: newDataSource,
                        selectDataSource: dao.selectDataSource.concat(selectDataSource),
                        selectedRowKeys: [],
                    })
                }}
                onCancel={() => hit({visible: false})}
                extraFooter={(<span>您已选择的供应商</span>)}
                content={(modalContent)}
            />
            <Table
                columns={columns}
                dataSource={dao.selectDataSource}
            />
        </div>
    );
}

export default connect(mapState, { changLoadingState })(Index);



